<template lang="">
    <transition name="slide-left-menu">
<div class="_full_router financial-content">
<!-- <mt-header :title="title"><mt-button icon="back" @click="goBack" slot="left">返回</mt-button></mt-header> -->
<custHeader :title="title"></custHeader>
<div class="normal-box search-condition">
    <div class="active">综合排序</div>
    <div>销量排序</div>
</div>
<div class="jqqd" v-if="list.length==0">
                {{tipMessage}}
            </div>
 <mt-loadmore :top-method="refresh" :bottom-method="loadMore" :bottom-all-loaded="loading" ref="loadmore" >
  <ul class="list-infinite cancel-link"  >
    <li v-for="(item,index) in list">
      <router-link :to="{path:'/shop/detail',query:{goodsCode:item.goodsCode,winNo:item.winNo,goodsName:item.goodsName}}">
      <div class="financial-box-2" @click="financialDetail(index)">
        <div class="img-contain">
            <img :src="img1">
        </div>
        <div class="text-contain">
          <div>{{ item.goodsName }}</div>
          <!-- <span class="badge">9折促销</span> -->
           <div class="qx"><span class="qx-text">价格:￥{{item.price1 | toMoney}}</span><span class="nhsyl-text">库存:{{item.inventoryQty-item.sellQty}}</span></div>
        </div>
      </div>
    </router-link>
    </li>
  </ul>
</mt-loadmore>
  <transition name="slide-right">
    <router-view class="cover-transition"></router-view>
  </transition>
</div>
</transition>
</template>
<script>
import cookieUtil from "js-cookie";
import custMixin from  'util/custMixin'
import custHeader from  'components/custHeader'
import {
  Navbar,
  TabItem,
  Loadmore,
  Header
} from "mint-ui";
import {
  Toast
} from "mint-ui";
Vue.use(Loadmore.name,Loadmore);
Vue.component(Header.name,Header);
export default {
  data() {
    return {
    title:"",
      tipMessage:"加载中...",
      cacheList: {},
      loading:false,
      list: [
    //   {
    //     stkName: "",
    //     code: "",
    //     expectRate: "",
    //     issPrice: "",
    //     subscribedLowAmt: ""
    // }
    ],
    allList:[],
     serverConfig:{
                //order:'',//排序 不填 默认字段
    //           stkex:global.scdm,//
    //           expectRateFlag:0,// 预期年绿化率" 0 全部
    //           minBuyAmt:0,//起购金额 0不限
    //           periodFlag:0,//剩余期限//0不限
    //           offset:0,
    //           limit:global.pageSize
              limit:global.pageSize,
              offset:"0",
              winNo:"",
              goodsCode:"",
              goodsStatus:"5",
          },
    stkBiz:global.STKBIZ.SUBSCRIBE      //资金申购  默认这个  不做认购申购
    };
  },

  computed:{
      img1:function(){
          return require('assets/logo.png');
      }
  },
  mixins:[custMixin],
  created: function() {
    this.getDataList().then(data=>{
       this.allList=data.rows;
    });



  },
  components:{
    custHeader
  },
  methods: {
    refresh:function(){
      this.serverConfig.offset=0;
      this.getDataList().then(data=>{
       this.allList=data.rows;
       this.$refs.loadmore.onTopLoaded();
    });

    },
    financialDetail: function(index) {
      //this.$router.push("/financial/detail?issNo=" + this.list[index].issNo);
    },
    getDataList:function(){

      var self = this;
    //   var pars={
    //       limit:global.pageSize,
    //       offset:"0",
    //       winNo:"",
    //       goodsCode:"",
    //       goodsStatus:"5",
    //   }
    //   axiosUtil.upShopSellQry(pars).then(data=>{
    //       var data=data.data;
    //       console.log(data)
    //   })
   return  axiosUtil.upShopSellQry(this.serverConfig).then(function(data) {
      var data=data.data;
             self.list = data.rows//默认所有
          if( self.list.length==0){
           self.tipMessage="暂无数据"
          }
          return data;
    })

    },
    loadMore: function() {
        let self = this;
        this.loading = true;
        this.serverConfig.offset += global.pageSize;
        this.getDataList(data=>{
          self.allList = self.allList.concat(data.rows);
           self.loading = false;
           if (data.total == self.allList.length) {
             self.loading = true;
             Toast({
               message: "已加载最后一条",
               position: "bottom",
               duration: 1000
             });
           }
        })
    }
  }
};
</script>
<style scoped>
._full_router {
  transition: .5s all ease;
}
.financial-box {
  display: -webkit-flex;
  /* Safari */
  display: flex;
}

.financial-box>div {
  flex-grow: 1;
}

.nhsyl {
  font-size: 26px;
  color: #ef4f4f;
}

.nhsyl-text {
  font-size: 14px;
  color: #bbb;
}

.qx {
  margin-top: 12px;
}

.qx-text {
  font-size: 14px;
  color: #7089ff;
  border-right: 1px solid #ddd;
  padding: 0 4px;
  margin-right: 4px;
}

.app-content {
  height: 100%;
  background: #111;
}

.financial-content {
  height: 100%;
  background: #efeee9;
}

::-webkit-scrollbar {
  width: 0px;
  height: 1px;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: rgba(0, 0, 0, 0.2);
}

.cancel-link  a{
  text-decoration: none;
  color: #111;
}
.list-infinite{
    width: 100%;

}
.list-infinite li{
    margin-top:0;
    padding: 0;
        border-bottom: 1px solid #eee;
}
.financial-box-2{
    display: flex;
}
.financial-box-2 .img-contain{
    width: 30vw;
}
.financial-box-2 .img-contain img{
    width: 100%;
}

.financial-box-2  .text-contain{
    padding: 12px;
}
.search-condition{
    background: #fff;
    border-bottom: 1px solid #eee;
    padding: 6px
}
.search-condition .active{
color: #FF6335;
}
.badge{
    font-size: 12px;
    color:#FF4545;
    border: 1px solid #FF4545;
    border-radius: 2px;
}
</style>



